<template>
  <div class="update-modal">
    <div class="update-modal__inner">
      <form
        ref="update_form"
        id="update_form"
        class="update-form"
        method="POST"
        enctype="multipart/form-data"
      >
        <div class="update-form__input-wrapper update-form__input-wrapper_name">
          <p v-if="form_respond" class="update-form__respond">
            <b>{{ form_respond }}</b>
          </p>
          <input
            v-model="product_name"
            name="product_name"
            class="update-form__input"
            placeholder="Название товара *"
          />
          <span
            class="update-form__input-error update-form__input-error_name"
            >{{ product_name_error }}</span
          >
        </div>
        <div
          class="update-form__input-wrapper update-form__input-wrapper_price"
        >
          <input
            v-model="product_price_new"
            name="product_price"
            class="update-form__input"
            placeholder="Новая цена / цена *"
            required
          />
          <span class="update-form__input-error update-form__input-error_price">
            {{ product_price_new_error }}</span
          >
        </div>
        <div
          class="update-form__input-wrapper update-form__input-wrapper_price"
        >
          <input
            v-model="product_price_old"
            name="product_price_old"
            class="update-form__input"
            placeholder="Старая цена / цена"
          />
          <span
            class="update-form__input-error update-form__input-error_old-price"
          ></span>
        </div>
        <div
          class="update-form__input-wrapper update-form__input-wrapper_vendor-code"
        >
          <input
            v-model="product_vendor_code"
            name="product_vendor_code"
            class="update-form__input"
            placeholder="Артикул"
          />
          <span
            class="update-form__input-error update-form__input-error_vendor-code"
            >{{ vender_code_error }}</span
          >
        </div>
        <div class="update-form__input-wrapper update-form__input-wrapper_file">
          <div class="input-file">
            <input
              required
              type="file"
              ref="file_update"
              name="product_img"
              id="update_main_img"
              @change="fileChangeUpdate($event)"
              class="update-form__input update-form__input_file"
            />
            <label for="update_main_img" class="input-file__button">
              <span class="input-file__icon-wrapper">
                <img
                  v-if="!showPreview && !product_file_path"
                  class="input-file__icon"
                  accept="image/*"
                  src="@/assets/img/download.svg"
                  alt="Выбрать файл"
                  width="25"
                />
                <img
                  class="input-file__icon input-file__icon_prev input-file__icon_prev_1"
                  v-bind:src="product_file_path"
                  v-show="product_file_path && !showPreview"
                />

                <img
                  class="input-file__icon input-file__icon_prev input-file__icon_prev_2"
                  v-bind:src="imagePreview"
                  v-show="showPreview"
                />
              </span>
              <span class="input-file__button-text">
                {{ product_file_status_text }}</span
              >
            </label>
          </div>
          <span class="update-form__input-error update-form__input-error_file">
            {{ is_file_upload_error }}</span
          >
        </div>
        <div
          class="update-form__input-wrapper update-form__input-wrapper_is-quantity"
        >
          <div class="input-checkbox">
            <input
              name="product_is_quantity"
              :checked="product_is_quantity == 1 ? true : false"
              id="input-checkbox__product-is-quantit"
              type="checkbox"
              class="update-form__checkbox"
              @change="isUpdateCount($event)"
              :value="product_is_quantity"
            />
            <label for="input-checkbox__product-is-quantit"
              >Выбор количества</label
            >
          </div>

          <span
            class="update-form__input-error update-form__input-error_is-quantity"
          ></span>
        </div>
        <div
          class="update-form__input-wrapper update-form__input-wrapper_is-fast-buy"
        >
          <div class="input-checkbox">
            <input
              name="product_is_fast_by"
              :checked="product_is_fast_by == 1 ? true : false"
              id="input-checkbox__product-is-fast-by"
              type="checkbox"
              class="update-form__checkbox"
              @change="isFastBuy($event)"
              :value="product_is_fast_by"
            />
            <label for="input-checkbox__product-is-fast-by"
              >Быстрая покупка</label
            >
          </div>

          <span
            class="update-form__input-error update-form__input-error_is-fast-buy"
          ></span>
        </div>
        <div
          class="update-form__input-wrapper update-form__input-wrapper_is_active"
        >
          <div class="input-checkbox">
            <input
              :checked="product_is_active == 1 ? true : false"
              name="product_is_active"
              id="input-checkbox__product-is-active"
              type="checkbox"
              class="update-form__checkbox"
              @change="isActiveChange($event)"
              :value="product_is_active"
            />
            <label for="input-checkbox__product-is-active">Активен</label>
          </div>

          <span
            class="update-form__input-error update-form__input-error_is-fast-buy"
          ></span>
        </div>
        <div class="update-form__submit-buttons">
          <button
            class="update-form__submit-button"
            type="submit"
            @click.prevent="submit()"
          >
            Сохранить изменения
          </button>
          <button class="update-form__submit-button" @click.prevent="close()">
            Закрыть
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: {
    product: Object,
  },
  data() {
    return {
      product_id: this.product.product_id,
      product_name: this.product.product_name,
      product_file_path: this.product.product_file_path,
      product_price_new: this.product.product_price,
      product_price_old: this.product.product_price_old,
      product_vendor_code: this.product.product_vendor_code,
      product_is_quantity: this.product.product_is_quantity,
      product_is_fast_by: this.product.product_is_fast_by,
      product_is_active: this.product.product_is_active,
      product_file_status_text: "Выберите фото *",
      is_file_upload: false,
      is_file_upload_error: "",
      formValid: false,
      form_respond: "",
      product_price_new_error: "",
      product_name_error: "",
      file: "",
      showPreview: false,
      imagePreview: "",
    };
  },
  // computed: {
  //   ...mapGetters(["GET_UPDATE_FORM_CONTENT"]),
  // },
  methods: {
    isActiveChange(e) {
      if (e.target.checked) {
        this.product_is_active = 1;
      } else {
        this.product_is_active = 0;
      }
      console.log(this.product_is_active);
    },
    validInputs() {
      if (
        this.product_name &&
        this.product_price_new &&
        (this.is_file_upload || this.product_file_path)
      ) {
        this.formValid = true;
      } else {
        if (!this.product_name) {
          this.product_name_error = "Укажите название товара";
        }

        if (!this.product_price_new) {
          this.product_price_new_error = "Укажите цену";
        }

        if (!this.is_file_upload && !this.product_file_path) {
          this.is_file_upload_error = "Добавьте фото";
        }
        setTimeout(() => {
          this.product_name_error = "";
          this.product_price_new_error = "";
          this.is_file_upload_error = "";
        }, 3000);

        this.formValid = false;
      }
    },
    fileChangeUpdate(e) {
      // console.log(e);
      console.log(e.target.value);
      if (e.target.value.length > 0) {
        this.file = this.$refs.file_update.files[0];
        let reader = new FileReader();
        reader.addEventListener(
          "load",
          function () {
            this.showPreview = true;
            this.imagePreview = reader.result;
          }.bind(this),
          false
        );

        if (this.file) {
          if (
            /\.(jpe?g|png|gif)$/i.test(this.file.name) &&
            this.file.name !== ""
          ) {
            reader.readAsDataURL(this.file);
            this.product_file_status_text = "Фото добавлено";
            this.is_file_upload = true;
          } else {
            this.$refs.file_update.value = null;
            this.file = "";
            this.is_file_upload = false;
            this.product_file_status_text = "Выберите фото *";
            this.is_file_upload_error = "Формат файла запрещен";
            this.product_file_path = "";
            // this.showPreview = false;
            // this.imagePreview = "";
            // this.file = "";
            // this.product_file_path = "";

            setTimeout(() => {
              this.is_file_upload_error = "";
            }, 3000);
          }
        }
      } else {
        this.product_file_status_text = "Выберите фото *";
        this.file = "";
        this.$refs.file_update.value = null;
        this.is_file_upload = false;
        this.imagePreview = "";
        this.showPreview = false;
      }
    },
    isUpdateCount(e) {
      if (e.target.checked) {
        this.product_is_quantity = 1;
      } else {
        this.product_is_quantity = 0;
      }
      console.log(e.target.value);
    },
    isFastBuy(e) {
      if (e.target.checked) {
        this.product_is_fast_by = 1;
      } else {
        this.product_is_fast_by = 0;
      }
      console.log(e.target.value);
    },
    submit() {
      this.validInputs();
      // console.log(this.formValid);
      // if (this.formValid) {
      let fd = new FormData(this.$refs.update_form);
      fd.append("product_id", this.product_id);
      fd.append("product_file_path", this.product_file_path);
      // fd.append("product_img", this.file);

      axios({
        url: "/admin/product/front/update_product.php",
        method: "post",
        headers: { "Content-Type": "multipart/form-data" },
        data: fd,
      })
        .then((response) => {
          console.log(response);
          if (response.data == "EXTENSIONERROR") {
            this.is_file_upload_error = "Запрещенный формат файла";
            this.is_file_upload = false;
            this.showPreview = false;
            this.imagePreview = "";
          }

          if (response.data == "BIGFILEERROR") {
            this.is_file_upload_error = "Уменьшите размер фото";
          }

          if (response.data == "LOADSUCCESS") {
            this.is_file_upload_error = "Фото загружено";
          }

          // if (response.data == "NOTUNIQUEVENDORCODE") {
          //   this.vender_code_error = "Такой артикул уже существует";
          // }

          if (response.data == "NOTUNIQUEPRODUCTIMG") {
            this.is_file_upload_error = "Такая картинка уже существует";
          }

          if (response.data == "UPDATEPRODUCTSUCCESS") {
            this.$store.commit("SET_ALERT_CONTENT", {
              title: "",
              description: "Товар успешно обновлен",
              status: "success",
              button_text_1: "ОК",
              button_text_2: "",
              is_open: true,
            });
            // this.product_file_path = "";
            this.$store.dispatch("PRODUCT_LIST_ACTION");
            this.close();
          }
        })
        .catch(function (error) {
          console.log(error);
        });

      setTimeout(() => {
        this.is_file_upload_error = "";
      }, 3000);

      setTimeout(() => {
        this.form_respond = "";
      }, 3000);

      setTimeout(() => {
        this.vender_code_error = "";
      }, 3000);
      // }

      // console.log(e.target);
      // console.log(this.$refs.update_form);
    },
    close() {
      this.$store.commit("SET_UPDATE_FORM_CONTENT", {
        product_id: "",
        product_name: "",
        product_file_path: "",
        product_price: "",
        product_price_old: "",
        product_vendor_code: "",
        product_is_quantity: "",
        product_is_fast_by: "",
        product_is_active: "",
      });
    },
  },
};
</script>

