import { createStore } from "vuex";
import Axios from "axios";

export default createStore({
  state: {
    product_list: {},
    alert_content: {
      title: "",
      description: "",
      status: "",
      button_text_1: "",
      button_text_2: "",
      is_open: false,
    },

    form_content: {
      product_id: "",
      product_name: "",
      product_file_path: "",
      product_price: "",
      product_price_old: "",
      product_vendor_code: "",
      product_is_quantity: "",
      product_is_fast_by: "",
      product_is_active: "",
    },

    config_list: "",
  },
  getters: {
    GET_PRODUCT_LIST_DATA: (state) => {
      return state.product_list;
    },
    GET_CONFIG_LIST_DATA: (state) => {
      return state.config_list;
    },
    GET_ALERT_CONTENT: (state) => {
      return state.alert_content;
    },

    GET_UPDATE_FORM_CONTENT: (state) => {
      return state.form_content;
    },
  },
  mutations: {
    SET_PRODUCT_LIST_DATA: (state, payload) => {
      state.product_list = payload;
    },
    SET_CONFIG_LIST_DATA: (state, payload) => {
      state.config_list = payload;
    },
    SET_ALERT_CONTENT: (state, payload) => {
      state.alert_content.title = payload.title;
      state.alert_content.description = payload.description;
      state.alert_content.status = payload.status;
      state.alert_content.button_text_1 = payload.button_text_1;
      state.alert_content.button_text_2 = payload.button_text_2;
      state.alert_content.is_open = payload.is_open;
    },
    SET_UPDATE_FORM_CONTENT: (state, payload) => {
      state.form_content.product_id = payload.product_id;
      state.form_content.product_name = payload.product_name;
      state.form_content.product_file_path = payload.product_file_path;
      state.form_content.product_price = payload.product_price;
      state.form_content.product_price_old = payload.product_price_old;
      state.form_content.product_vendor_code = payload.product_vendor_code;
      state.form_content.product_is_quantity = payload.product_is_quantity;
      state.form_content.product_is_fast_by = payload.product_is_fast_by;
      state.form_content.product_is_active = payload.product_is_active;
    },
  },
  actions: {
    PRODUCT_LIST_ACTION: async (context) => {
      let { data } = await Axios.post(
        "/admin/product/front/get_product_list.php"
      );
      context.commit("SET_PRODUCT_LIST_DATA", data);
    },

    CONFIG_LIST_ACTION: async (context) => {
      let { data } = await Axios.post("/admin/config/front/get_config.php");
      context.commit("SET_CONFIG_LIST_DATA", data);
    },
  },
  modules: {},
});
