<template>
  <form
    ref="product_form"
    id="product_form"
    class="product-form"
    method="POST"
    enctype="multipart/form-data"
  >
    <div class="product-form__input-wrapper product-form__input-wrapper_name">
      <p v-if="form_respond" class="product-form__respond">
        <b>{{ form_respond }}</b>
      </p>
      <input
        v-model="product_name"
        name="product_name"
        class="product-form__input"
        placeholder="Название товара *"
      />
      <span class="product-form__input-error product-form__input-error_name">{{
        product_name_error
      }}</span>
    </div>
    <div class="product-form__input-wrapper product-form__input-wrapper_price">
      <input
        v-model="product_price_new"
        name="product_price"
        class="product-form__input"
        placeholder="Новая цена / цена *"
        required
      />
      <span class="product-form__input-error product-form__input-error_price">
        {{ product_price_new_error }}</span
      >
    </div>
    <div class="product-form__input-wrapper product-form__input-wrapper_price">
      <input
        v-model="product_price_old"
        name="product_price_old"
        class="product-form__input"
        placeholder="Старая цена / цена"
      />
      <span
        class="product-form__input-error product-form__input-error_old-price"
      ></span>
    </div>
    <div
      class="product-form__input-wrapper product-form__input-wrapper_vendor-code"
    >
      <input
        v-model="vender_code"
        name="product_vendor_code"
        class="product-form__input"
        placeholder="Артикул"
      />
      <span
        class="product-form__input-error product-form__input-error_vendor-code"
        >{{ vender_code_error }}</span
      >
    </div>
    <div class="product-form__input-wrapper product-form__input-wrapper_file">
      <div class="input-file">
        <input
          required
          type="file"
          ref="file_add"
          name="product_img"
          id="main_img"
          @change="fileChange($event)"
          class="product-form__input product-form__input_file"
        />
        <label for="main_img" class="input-file__button">
          <span class="input-file__icon-wrapper">
            <img
              v-if="!showPreview && !product_file_path"
              class="input-file__icon"
              accept="image/*"
              src="@/assets/img/download.svg"
              alt="Выбрать файл"
              width="25"
            />
            <img
              class="input-file__icon input-file__icon_prev input-file__icon_prev_1"
              v-bind:src="product_file_path"
              v-show="product_file_path && !showPreview"
            />

            <img
              class="input-file__icon input-file__icon_prev input-file__icon_prev_2"
              v-bind:src="imagePreview"
              v-show="showPreview"
            />
          </span>
          <span class="input-file__button-text">
            {{ product_file_status_text }}</span
          >
        </label>
      </div>
      <span class="product-form__input-error product-form__input-error_file">
        {{ is_file_upload_error }}</span
      >
    </div>
    <div
      class="product-form__input-wrapper product-form__input-wrapper_is-quantity"
    >
      <div class="input-checkbox">
        <input
          name="product_is_quantity"
          id="input-checkbox__is-quantity"
          type="checkbox"
          class="product-form__checkbox"
          @change="isProductCount($event)"
          :value="is_product_count"
          :checked="is_product_count > 0 ? true : false"
        />
        <label for="input-checkbox__is-quantity">Выбор количества</label>
      </div>

      <span
        class="product-form__input-error product-form__input-error_is-quantity"
      ></span>
    </div>
    <div
      class="product-form__input-wrapper product-form__input-wrapper_is-fast-buy"
    >
      <div class="input-checkbox">
        <input
          name="product_is_fast_by"
          id="input-checkbox__is-fast-buy"
          type="checkbox"
          class="product-form__checkbox"
          @change="isFastBuy($event)"
          :value="is_fast_buy"
        />
        <label for="input-checkbox__is-fast-buy">Быстрая покупка</label>
      </div>

      <span
        class="product-form__input-error product-form__input-error_is-fast-buy"
      ></span>
    </div>
    <button class="submit-button" type="submit" @click="submit($event)">
      Добавить товар
    </button>
  </form>
</template>

<script>
import axios from "axios";

export default {
  components: {},
  data() {
    return {
      product_name: "",
      product_name_error: "",
      product_price_new: "",
      product_price_new_error: "",
      product_price_old: "",
      vender_code: "",
      vender_code_error: "",
      product_type: "",
      is_product_count: 0,
      is_fast_buy: 0,
      product_file_status_text: "Выберите фото *",
      is_file_upload: false,
      is_file_upload_error: "",
      formValid: false,
      form_respond: "",
      product_list: {},

      file: "",
      showPreview: false,
      imagePreview: "",
    };
  },
  methods: {
    validInputs() {
      if (this.product_name && this.product_price_new && this.is_file_upload) {
        this.formValid = true;
      } else {
        if (!this.product_name) {
          this.product_name_error = "Укажите название товара";
        }

        if (!this.product_price_new) {
          this.product_price_new_error = "Укажите цену";
        }

        if (!this.is_file_upload) {
          this.is_file_upload_error = "Добавьте фото";
        }
        setTimeout(() => {
          this.product_name_error = "";
          this.product_price_new_error = "";
          this.is_file_upload_error = "";
        }, 3000);

        this.formValid = false;
      }
    },
    fileChange(e) {
      // if (e.target.value.length > 0) {
      //   this.product_file_status_text = "Фото добавлено";
      //   this.is_file_upload = true;
      // } else {
      //   this.product_file_status_text = "Выберите фото *";
      //   this.is_file_upload = false;
      // }

      if (e.target.value.length > 0) {
        this.file = this.$refs.file_add.files[0];
        let reader = new FileReader();
        reader.addEventListener(
          "load",
          function () {
            this.showPreview = true;
            this.imagePreview = reader.result;
          }.bind(this),
          false
        );

        if (this.file) {
          if (/\.(jpe?g|png|gif)$/i.test(this.file.name)) {
            reader.readAsDataURL(this.file);
            this.product_file_status_text = "Фото добавлено";
            this.is_file_upload = true;
          } else {
            this.is_file_upload = false;
            this.showPreview = false;
            this.imagePreview = "";
            this.file = "";
            this.product_file_path = "";
            this.product_file_status_text = "Выберите фото *";
            this.is_file_upload_error = "Формат файла запрещен";
            setTimeout(() => {
              this.is_file_upload_error = "";
            }, 3000);
          }
        }
      } else {
        this.product_file_status_text = "Выберите фото *";
      }
    },
    isProductCount(e) {
      if (e.target.checked) {
        this.is_product_count = 1;
      } else {
        this.is_product_count = 0;
      }
      console.log(e.target.value);
    },
    isFastBuy(e) {
      if (e.target.checked) {
        this.is_fast_buy = 1;
      } else {
        this.is_fast_buy = 0;
      }
      console.log(e.target.value);
    },
    submit(e) {
      e.preventDefault();
      this.validInputs();

      if (this.formValid) {
        let fd = new FormData(this.$refs.product_form);

        axios({
          url: "/admin/product/front/add_product.php",
          method: "post",
          processData: false,
          contentType: false,
          data: fd,
        })
          .then((response) => {
            console.log(response);
            if (response.data == "EXTENSIONERROR") {
              this.is_file_upload_error = "Запрещенный формат файла";
            }

            if (response.data == "BIGFILEERROR") {
              this.is_file_upload_error = "Уменьшите размер фото";
            }

            if (response.data == "LOADSUCCESS") {
              this.is_file_upload_error = "Фото загружено";
            }

            // if (response.data == "NOTUNIQUEVENDORCODE") {
            //   this.vender_code_error = "Такой артикул уже существует";
            // }

            if (response.data == "NOTUNIQUEPRODUCTIMG") {
              this.is_file_upload_error = "Такая картинка уже существует";
            }

            if (response.data == "ADDPRODUCTSUCCESS") {
              this.$store.dispatch("PRODUCT_LIST_ACTION");
              this.product_name = "";
              this.product_price_new = "";
              this.product_price_old = "";
              this.is_product_count = 0;
              this.is_q;
              this.vender_code = "";
              this.is_fast_buy = 0;
              this.file = "";
              this.showPreview = false;
              this.imagePreview = "";
              this.product_file_status_text = "Выберите фото *";
              this.is_file_upload = false;
              console.log((this.$refs.file_add.value = ""));
              // вызов мутации алерта
              this.$store.commit("SET_ALERT_CONTENT", {
                title: "Успех",
                description: "Товар успешно сохранен",
                status: "success",
                button_text_1: "ОК",
                button_text_2: "",
                is_open: true,
              });
            }

            if (response.data == "EMPTYERROR") {
              this.$store.commit("SET_ALERT_CONTENT", {
                title: "Ошибка",
                description: "Заполните все поля, отмеченные звёздочкой",
                status: "error",
                button_text_1: "ОК",
                button_text_2: "",
                is_open: true,
              });
            }
          })
          .catch(function (error) {
            console.log(error);
          });

        setTimeout(() => {
          this.is_file_upload_error = "";
        }, 3000);

        setTimeout(() => {
          this.form_respond = "";
        }, 3000);

        setTimeout(() => {
          this.vender_code_error = "";
        }, 3000);
      }

      // console.log(e.target);
      // console.log(this.$refs.product_form);
    },
  },
};
</script>

