<template>
  <header class="app-header">
    <img src="/admin/img/smartbasket.svg" height="50" />
    <div class="app-header__content">
      <button
        @click="logout"
        class="app-header__button app-header__button_logout"
      >
        Выйти
      </button>
    </div>
  </header>
</template>

<script>
import axios from "axios";
export default {
  methods: {
    logout() {
      axios.post("/admin/auth/front/log_out.php").then((response) => {
        console.log(response);
        if (response.data == "LOGOUT") {
          document.location.href = "/admin/";
        }
      });
    },
  },
};
</script>

