<template>
  <form
    ref="product_item"
    id="product-item"
    class="product-item"
    method="POST"
    enctype="multipart/form-data"
  >
    <div class="product-item__name-wrapper">
      <div class="product-item__img-wrapper">
        <img
          ref="product-item__preview"
          :src="product_file_path"
          :alt="product_name"
        />
        <!-- <button class="product-item__img-button">Изменить</button> -->
      </div>
      <div
        class="product-item__input-wrapper product-item__input-wrapper_title"
      >
        <input
          name="product_name"
          class="product-item__input product-item__input_name"
          :class="product_name.length <= 0 ? 'error' : ''"
          placeholder="Название товара *"
          v-model="product_name"
        />
        <span class="product-item__error product-item__error_title">{{
          product_name_error
        }}</span>
      </div>
    </div>
    <div class="product-item__input-wrapper product-item__input-wrapper_price">
      <input
        v-model="product_price_new"
        name="product_price"
        class="product-item__input product-item__input_price"
        :class="product_price_new.length <= 0 ? 'error' : ''"
        placeholder="Новая цена / цена *"
        @keyup="validInputPriceNew()"
        required
      />
      <span class="product-item__error product-item__error_price">{{
        product_price_new_error
      }}</span>
    </div>
    <div class="product-item__is-active-wrapper">
      <input
        :checked="product_is_active == 1 ? true : false"
        name="product_is-active"
        id="input-checkbox__is-active"
        type="checkbox"
        class="product-form__checkbox"
        @change="isActiveChange($event)"
      />
      <label for="input-checkbox__is-active">активен</label>
    </div>
    <div class="product-item__setting-wrapper">
      <button
        @click.prevent="changeProduct()"
        class="product-item__modal-button"
      >
        Подробнее
      </button>
      <button
        @click.prevent="submit()"
        class="product-item__save-button product-item__save-button_add"
      >
        Сохранить
      </button>
      <button
        @click.prevent="del()"
        class="product-item__save-button product-item__save-button_dev"
      >
        Удалить
      </button>
    </div>
  </form>

  <!-- <update-form :product="product" /> -->
</template>

<script>
// ^(?!0[^.\n])\d+(?:.\d+)?$ регулярка для воода цифр
// ^(?!\.|.*\.$)(?=[^.]*\.?[^.]*$).{1,10}$ - только одна точка в строке
import axios from "axios";

export default {
  name: "ProductList",
  components: {
    // UpdateForm,
  },
  props: {
    product: Object,
  },
  data() {
    return {
      product_id: this.product.product_id,
      product_name: this.product.product_name,
      product_file_path: this.product.product_img,
      product_price_new: this.product.product_price,
      product_price_old: this.product.product_price_old,
      product_vendor_code: this.product.product_vendor_code,
      product_is_quantity: this.product.product_is_quantity,
      product_is_fast_by: this.product.product_is_fast_by,
      product_is_active: this.product.product_is_active,
      product_name_error: "",
      product_price_new_error: "",
      formValid: false,
      form_respond: "",
    };
  },

  methods: {
    isActiveChange(e) {
      if (e.target.checked) {
        this.product_is_active = 1;
      } else {
        this.product_is_active = 0;
      }
    },
    validInputPriceNew() {
      // let regexp = new RegExp(^(?!\.|.*\.$)(?=[^.]*\.?[^.]*$).{1,10}$);
      this.product_price_new = this.product_price_new.replace(
        /^\.|[^\d\\.]/g,
        ""
      );
      // this.product_price_new = this.product_price_new.replace(/,/g, ".");
      // this.product_price_new =
      //   this.product_price_new == 0 ? "" : this.product_price_new;
      // this.product_price_new =
      //   this.product_price_new[0] == "." ? "" : this.product_price_new;

      // this.product_price_new = this.product_price_new.replace("..", ".");
      // this.product_price_new = this.product_price_new.replace("...", ".");
      // this.product_price_new = this.product_price_new.replace(
      //   /^([^\\.]*\.)|\./g,
      //   "$1"
      // );
      // this.product_price_new = this.product_price_new.replace(",", ".");
      if (this.product_price_new.length) {
        console.log("error");
      }
    },
    validInputs() {
      if (
        this.product_name &&
        this.product_price_new &&
        this.product_file_path
      ) {
        this.formValid = true;
      } else {
        if (!this.product_name) {
          this.product_name_error = "Укажите название товара";
        }

        if (!this.product_price_new) {
          this.product_price_new_error = "Укажите цену";
        }

        if (!this.product_file_path) {
          this.is_file_upload_error = "Добавьте фото";
        }
        setTimeout(() => {
          this.product_name_error = "";
          this.product_price_new_error = "";
          this.is_file_upload_error = "";
        }, 3000);

        this.formValid = false;
      }
    },
    del() {
      let product_id = this.product_id;
      axios({
        url: "/admin/product/front/delete_product.php",
        method: "post",
        headers: { "Content-Type": "multipart/form-data" },
        data: {
          product_id: product_id,
        },
      }).then((response) => {
        console.log(response);
        this.$store.dispatch("PRODUCT_LIST_ACTION");
      });
    },
    submit() {
      this.validInputs();
      if (this.formValid) {
        // let fd = new FormData(this.$refs.product_item);
        let product_id = this.product_id;
        let product_name = this.product_name;
        let product_file_path = this.product_file_path;
        let product_price_new = this.product_price_new;
        let product_price_old = this.product_price_old;
        let product_vendor_code = this.product_vendor_code;
        let product_is_active = this.product_is_active;
        let product_is_fast_by = this.product_is_fast_by;
        let product_is_quantity = this.product_is_quantity;

        axios({
          url: "/admin/product/front/update_product.php",
          method: "post",
          headers: { "Content-Type": "multipart/form-data" },
          // processData: false,
          // contentType: false,
          // data: fd,
          data: {
            product_id: product_id,
            product_name: product_name,
            product_file_path: product_file_path,
            product_price: product_price_new,
            product_price_old: product_price_old,
            product_vendor_code: product_vendor_code,
            product_is_quantity: product_is_quantity,
            product_is_fast_by: product_is_fast_by,
            product_is_active: product_is_active,
          },
        }).then((response) => {
          this.$store.commit("SET_ALERT_CONTENT", {
            title: "",
            description: "Товар успешно обновлен",
            status: "success",
            button_text_1: "ОК",
            button_text_2: "",
            is_open: true,
          });
          console.log(response);
        });
      } else {
        this.$store.commit("SET_ALERT_CONTENT", {
          title: "Упс",
          description: "Не удалось обновить товар",
          status: "error",
          button_text_1: "ОК",
          button_text_2: "",
          is_open: true,
        });
        console.log("not-send");
      }
    },

    changeProduct() {
      this.$store.commit("SET_UPDATE_FORM_CONTENT", {
        product_id: this.product_id,
        product_name: this.product_name,
        product_file_path: this.product_file_path,
        product_price: this.product_price_new,
        product_price_old: this.product_price_old,
        product_vendor_code: this.product_vendor_code,
        product_is_quantity: this.product_is_quantity,
        product_is_fast_by: this.product_is_fast_by,
        product_is_active: this.product_is_active,
      });
    },
  },
};
</script>

